import React from 'react'
import { BrowserRouter, Route, Switch } from 'react-router-dom'

import Home from './views/home/Home'
import Contato from './views/contato'
import Fotos from './views/fotos'
import Espec from './views/espec'

const Routes = () => {
  return (
    <BrowserRouter>
      <Route
        path='https://marcosdid.github.io/GoogleGlassReact/'
        component={Home}
        exact
      />
      <Route
        path='https://marcosdid.github.io/GoogleGlassReact/contato'
        component={Contato}
      />
      <Route
        path='https://marcosdid.github.io/GoogleGlassReact/fotos'
        component={Fotos}
      />
      <Route
        path='https://marcosdid.github.io/GoogleGlassReact/especificacoes'
        component={Espec}
      />
    </BrowserRouter>
  )
}

export default Routes
